<template>
  <div>
    <input type="text" v-model="userId" />
    <p>{{ userId }}</p>
    <button v-on:click="myFunction">클릭</button>
    <button @click="myChangeData">변경</button>
    <br><br>
    <p>String</p>
    <input type="text" v-model="num1" /> +
    <input type="text" v-model="num2" /> =
    <span>{{ num1 + num2 }}</span>
    <br><br>
    <p>Number</p>
    <input type="text" v-model.number="num2" /> +
    <input type="text" v-model.number="num3" /> =
    <span>{{ num2 + num3 }}</span>
  </div>
</template>
<script>
export default {
  components: {},
  data() {
    return {
      userId: 'jihun.go',
      num1: 5,
      num2: 4,
      num3: 3
    }
  },
  setup() {},
  created() {},
  mounted() {},
  unmounted() {},
  methods: {
    myFunction() {
      console.log(this.userId)
    },
    myChangeData() {
      this.userId = 'jeremy'
    }
  }
}
</script>
